import React, { useState } from "react";
import "./Login.scss";
import { useNavigate } from "react-router-dom";

export const Login = () => {
  const [data, setdata] = useState({
    email: "example@gmail.com",
    password: "123456",
  });
  let handleInput = (e) => {
    let { name, value } = e.target;
    setdata({
      ...data,
      [name]: value,
    });
  };

  const navigate = useNavigate();
  return (
    <div className="login flex justify-center align-center w-full h-full d-flex  p-6 items-center flex-wrap">
      <div className="loginImg w-full sm:w-8/12  sm:p-8">
        <img
          className="rounded-xl w-full object-cover "
          src="https://fooddesk.dexignlab.com/react/demo/static/media/pic-5.7dba4fa4b2b813e643ad.jpg"
          alt=""
        />
      </div>
      <div className="loginForm w-full sm:w-4/12  text-center">
        <div className="logo w-full">
          <img
            className="m-auto "
            src="data:image/png;base64,"
            alt=""
          />
        </div>
        <h3 className="text-xl font-bold  py-2">Create an Account</h3>
        <div className="formUnderline w-full d-flex justify-between items-start">
          <span className="w-20 h-0.5 inline-block bg-gray-200"></span>
          <span className="text-themegray inline-block px-4">Sign Up</span>
          <span className="w-20 h-0.5 inline-block bg-gray-200"></span>
        </div>
        <form action="" className="text-left"  onSubmit={() => (data.email ? navigate("/") : navigate("/login"))}>
          <div className="email w-full py-2">
            <label
              htmlFor=""
              className="block font-semibold text-sm pb-1 text-themegray text"
            >
              Email Adress
            </label>
            <input
              type="email"
              value={data.email}
              className="p-2 outline-none border-gray-300 text-themegray border w-full rounded-lg"
              name="email"
              onChange={handleInput}
              required
            />
          </div>
          <div className="password w-full py-2">
            <label
              htmlFor=""
              className="block  font-semibold text-sm pb-1 text-themegray text"
            >
              Password
            </label>
            <input
              type="password"
              value={data.password}
              className="p-2 outline-none border-gray-300 text-gray-500 border w-full rounded-lg"
              name="password"
              onChange={handleInput}
              required
            />
          </div>
          <div className="formCheckbox my-4">
            <input type="checkbox" name="" id="check" />
            <i class="fas fa-check"></i>

            <label
              htmlFor="check"
              className="-ml-1 text-themegray font-semibold"
            >
              Remember my preference
            </label>
          </div>
          <button
           
            class="bg-themeorange hover:bg-themedeeporange text-white w-full font-semibold py-2 px-4 rounded-lg my-2"
          >
            Sign Me In
          </button>
        </form>
        <div className="formUnderline w-full d-flex justify-between items-start">
          <span className="w-20 h-0.5 inline-block bg-gray-200"></span>
          <span className="text-themegray inline-block px-4">
            Continue With
          </span>
          <span className="w-20 h-0.5 inline-block bg-gray-200"></span>
        </div>
        <div className="socialMedia gap-1 w-full flex items-center justify-center my-5">
          <div className="facebook p-4 py-2 rounded border">
            <i class="fab fa-facebook me-2 facebook text-blue-700"></i>
            <span>Facebook</span>
          </div>
          <div className="google px-4 py-2 rounded border">
            <i class="fab fa-google me-2 google text-red-500"></i>
            <span>Google</span>
          </div>
          <div className="likedin px-4 py-2 rounded border">
            <i class="fab fa-linkedin me-2 likedin text-sky-500"></i>
            <span>linkedin</span>
          </div>
        </div>
        <div className="signup">
          <p className="text-sm space-x-28 text-themegray">
            Don't have an account{" "}
            <a href="" className="text-themeorange">
              Sign Up
            </a>
          </p>
        </div>
      </div>
    </div>
  );
};
